import React, { useContext } from "react";

import Navbar from "../components/Navbar.jsx";
import Cover from "../components/Cover.jsx";
import Footer from "../components/Footer.jsx";

import wallets from "../utils/Wallets.js";
import { DataContext } from "../utils/Data.js";

const Wallet = () => {
  const { data, setData, isConnected } = useContext(DataContext);
  const { wallet, address } = data;

  return (
    <>
      <Navbar theme="dark" />
      <Cover title="Wallet" />
      <main className="mx-auto mt-8 p-8 lg:p-16 w-full flex flex-wrap justify-center">
        {wallets.map((e) => (
          <div className="group relative w-full sm:w-1/2 md:w-1/3 xl:w-1/4 2xl:w-1/5 p-2 lg:p-4 select-none">
            <div
              onClick={
                !isConnected() ? e.connector?.bind(null, setData, e.name) : null
              }
              className={`h-full w-full rounded-3xl p-4 md:p-6 border-2 border-gray-200 flex items-start flex-col gap-2 ${
                typeof e.connector === "function"
                  ? isConnected()
                    ? "cursor-default"
                    : "cursor-pointer"
                  : "cursor-not-allowed opacity-70"
              }`}
            >
              <img
                className="h-12 w-12 lg:h-16 lg:w-16 rounded-full transform group-hover:scale-125 transition"
                src={e.logo}
                alt="logo"
              />
              <h3 className="mt-2 text-lg font-bold relative">
                {e.name}
                {wallet === e.name && isConnected() && (
                  <div className="h-3 w-3 absolute top-1/2 -right-6 rounded-full bg-green-500 transform -translate-y-1/2"></div>
                )}
                <div className="h-0.5 w-0 group-hover:w-full bg-purple-700 transition-all"></div>
              </h3>
              <p className="text-sm text-gray-500">
                {"Address: " +
                  (isConnected() && wallet === e.name
                    ? address?.slice(0, 6) + "...." + address?.slice(-4)
                    : "-")}
                {wallet === e.name && isConnected() && (
                  <button
                    onClick={e.disconnector.bind(null, setData)}
                    className="ml-2 px-2 text-base bg-red-500 text-gray-200 rounded-full relative"
                  >
                    x
                  </button>
                )}
              </p>
              <p className="text-gray-500">{e.description}</p>
            </div>
            {e.highlight && (
              <span className="absolute top-6 right-6 lg:top-12 lg:right-12 px-3 bg-gray-200 text-sm lg:text-base text-gray-500 rounded-full">
                {e.highlight}
              </span>
            )}
          </div>
        ))}
      </main>
      <Footer />
    </>
  );
};

export default Wallet;
