import React, { useState } from "react";

export const defaultData = {
  wallet: null,
  chainID: null,
  address: null,
};

export const DataContext = React.createContext();

const Data = ({ children }) => {
  const [data, setData] = useState(defaultData);

  const isConnected = () => data.wallet !== null && data.address !== null;

  return (
    <DataContext.Provider value={{ data, setData, isConnected }}>
      {children}
    </DataContext.Provider>
  );
};

export default Data;
