import { defaultData } from "./Data.js";

const getAccounts = async () =>
  await window.ethereum.request({
    method: "eth_requestAccounts",
  });

const enable = (eth, setData, wallet) => {
  eth.on("accountsChanged", (accounts) => {
    if (!accounts[0]) return setData(defaultData);

    setData({ chainID: eth.chainId, address: accounts[0], wallet });
  });

  eth.on("chainChanged", async (chainID) => {
    const accounts = await getAccounts();

    setData({ chainID, address: accounts[0], wallet });
  });
};

const connector = async (setData, wallet) => {
  if (!window.ethereum) return alert("Opps! MetaMask isn't detected");

  const eth = window.ethereum;
  const accounts = await getAccounts();

  setData({ chainID: eth.chainId, address: accounts[0], wallet });
  enable(eth, setData, wallet);
};

const disconnector = (setData) => setData(defaultData);

export { connector, disconnector };
